import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const uiModule = {
    state: {
        count: 15,
        user:{
            name:""
        },
        system:{
           name:""
        }
    },
    mutations: {
        incrementDouble(state) {
            state.count++;
        },
        setUser(state, user) {
            for (var k in user) {
                state.user[k] = user[k];
            }
        },
        setSystem(state, user) {
            for (var k in user) {
                state.system[k] = user[k];
            }
        },
    },
    getters: {
        doubleCount(state) {
            return state.count * 2
        }
    }
}

const store = new Vuex.Store({
    modules: {
        ui: uiModule
    },
    state: {
        count: 0,
        routeTree: null,
        lhttp:{
            // platform_token:"twxpjjs7jfmpskhotw2djkxr5explkhn",
            urlHead: 'http://192.168.10.247:8099/crm-api/v2',
            urlHeads: 'http://192.168.10.247/crm-api',
            AddToBDPrivate:"/shop/public/addToBDPrivate",//
            addToPrivate:"/shop/public/addToPrivate",//
            create:"/shop/public/create",//
            search:"/shop/public/search",//
            applyShopDetail:"/shop/private/findApplyingShopDetail",//
            fllowShopDetail:"/shop/private/findFollowingShopDetail",//
            giveup:"/shop/private/giveUp",//
            enterShop:"/shop/private/listEnteredShops",//
            notEnterShop:"/shop/private/notEnterShops",//
            shopCount:"/shop/private/shopCount",//
            areaLists: '/cash/area/list',//
        }
    },
    mutations: {
        initRoute(state, params) {
            console.log(params);
            state.routeTree = params.routeTree
        },
        increment(state, params) {
            state.count++;
        }
    }
})

export default store
