import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'

Vue.use(VueRouter)
Vue.use(Vuex)

// 
const Home = () => import('./pages/Public.vue')
const About = () => import('./pages/About.vue')
const Public = () => import('./pages/Public.vue')
// const PublicSettled = () => import('./pages/PublicSettled.vue')
// const PublicUnsettled = () => import('./pages/PublicUnsettled.vue')
const  Publicdetails = ()=>import("./pages/Publicdetails.vue");
const Private = () => import('./pages/Private.vue')
const PrivateSettled = () => import('./pages/PrivateSettled.vue')
const Privatesettledeails =()=>import("./pages/Privatesettledeails.vue")
const PrivateUnsettled = () => import('./pages/PrivateUnsettled.vue')
const Privateunsettledetials=()=>import("./pages/Privateunsettledetials.vue")
const newPoint = () => import("./pages/new_point.vue")
const changeMessage = () => import("./pages/changeMessage.vue")
const newpoint2 = () => import("./pages/newpoint2.vue")
const chooseMap = () => import("./pages/chooseMap.vue")
const gdMap     = () =>import("./pages/gdMap.vue")
// const networkdetails = () => import("./pages/networkdetails.vue")
//  / 
//  renderTree: false
//  meta singleRouter: true
let routeTree = [{
    path: '/',
    component: Home,
    sub: [
        {
            path: '/public', component: Public, meta: { level: '', singleRouter:true}, renderTree: true,
            // sub:[]
            sub: [
                {path: '/public/publicdetails', component: Publicdetails, meta: { level: '/' }},
                    // sub: [
                    //     // {
                    //     //     path: '/public/settled/about',
                    //     //     component: About,
                    //     //     meta: { level: '//' },
                    //     //     renderTree: false
                    //     // },
                    // ]
                    { path: "/public/newpoint", component: newPoint, meta: { level: "/" }},
                    { path: "/public/newpoint2", component: newpoint2, meta: { level: "/" }}
                
                // { path: '/public/unsettled', component: PublicUnsettled, meta: { level: '/' } }
            ]
        },
        {
            path: '/private', component: Private, meta: { level: '' }, renderTree: true,
            sub: [
                {
                    path: '/private/settled', component: PrivateSettled, meta: { level: '/' },
                    sub: [
                        {
                            path: "/private/settled/privatesettledeails", component: Privatesettledeails, meta: { level: "//" }
                        }
                    ]
                },
                { path: '/private/unsettled', component: PrivateUnsettled, meta: { level: '/' },
                sub: [
                    {
                        path: "/private/unsettled/privateunsettledetials", component: Privateunsettledetials, meta: { level: "//" }
                    }
                ]
                }

            ]
        },
        // {
        //     path: "/newpoint", component: newPoint, meta: { level: "-" }
        // },
        {
            path: "/changemessage", component: changeMessage, meta: { level: "" }
        },
        // {
        //     path: "/newpoint2", component: newpoint2, meta: { level: "-" }
        // },
        {
            path: "/choosemap", component: gdMap, meta: { level: "" }
        }

    ],
}];

function singleRouterify(tree) {
    tree.forEach((route, index) => {
        if (route.meta.singleRouter) {
            route.sub.forEach(item => {

                item.meta.singleRouter = true;
                if (item.sub) singleRouterify(item.sub)
            })
        }
    });

}

singleRouterify(routeTree[0].sub);


// 
const router = new VueRouter({
    routes: constructRoute(routeTree)
})

router.beforeEach((to, from, next) => {
    console.log(to);
    if (!router.app.$store.state.routerTree) {
        router.app.$store.commit('initRoute', { routeTree: routeTree });
    }
    next();
})

function constructRoute(tree) {
    var routes = [];
    construct(tree);

    function construct(tree) {
        tree.forEach((route, index) => {
            routes.push(route);
            if (route.sub) {
                construct(route.sub);
            }
        });
    }

    return routes;
}

export default router
