<template>
  <div>
    <mt-popup v-model="popupVisible" position="bottom" class="animated" :class="{slideInUp:popupVisible,slideOutDown:!popupVisible}">
      <ul class="pop_top">
        <li @click="qx(false)">
          <span></span>
        </li>
        <li>
          <span></span>
        </li>
        <li>
          <span></span>
        </li>
        <li @click="qx(true)">
          <span></span>
        </li>
      </ul>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </mt-popup>
  </div>
</template>

<script>
import { Popup, Picker } from "mint-ui";
export default {
  props: ["showpop", "data"],
  data() {
    return {
      slots: [
        {
          flex: 1,
          values: this.data[0],
          className: "slot1",
          textAlign: "right"
        },
        {
          divider: true,
          content: "-",
          className: "slot2"
        },

        {
          flex: 1,
          values: this.data[1],
          className: "slot3",
          textAlign: "left"
        }
      ],
      value: ""
    };
  },
  computed: {
    popupVisible: {
      get() {
        return this.showpop;
      },
      set(newVal) {
        return newVal;
      }
    }
  },
  created() {
    this.select();
  },
  watch: {},
  methods: {
    select() {
      if (Object.prototype.toString.call(this.data) === "[object Object]") {
        this.slots[0].values = Object.keys(this.data);
        let a = [];
        Object.values(this.data).forEach(val => {
          a.push(...val);
        });
        this.slots[2].values = a;
      } else if (
        Object.prototype.toString.call(this.data) === "[object Array]"
      ) {
        if (this.data.length == 2) {
          this.slots = [
            {
              flex: 1,
              values: this.data[0],
              className: "slot1",
              textAlign: "right"
            },
            {
              divider: true,
              content: "-",
              className: "slot2"
            },

            {
              flex: 1,
              values: this.data[1],
              className: "slot3",
              textAlign: "left"
            }
          ];
        } else if (this.data.length == 3) {
          this.slots = [
            {
              flex: 1,
              values: this.data[0],
              className: "slot1",
              textAlign: "right"
            },
            {
              divider: true,
              content: "-",
              className: "slot2"
            },

            {
              flex: 1,
              values: this.data[1],
              className: "slot3",
              textAlign: "left"
            },
             {
              divider: true,
              content: "-",
              className: "slot3"
            }, 
            {
              flex: 1,
              values: this.data[2],
              className: "slot4",
              textAlign: "left"
            },
          ];
          // this.slots[0].values = this.data[0];
          // this.slots[2].values = this.data[1];
          // this.slots[4].values = this.data[2];
        }
      }
    },
    onValuesChange(picker, values) {
      this.value = values;
      this.$emit("change", {
        obj: picker,
        values: values
      });
    },

    qx(val) {
      if (val) {
        this.$emit("popVal", this.value + "");
      } else {
        this.$emit("popVal", false);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.picker-item {
  height: 44px;
  line-height: 44px;
}
ul {
  margin: 0px;
  padding: 0px;
  list-style-type: none;
  display: block;
  box-sizing: content-box;
  li {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 52px;
    padding: 0px;
    margin: 0px;
    font-family: "PingFangSC-Regular";
    font-size: 16px;
    color: #444444;
    text-align: center;
  }
}
.mint-popup {
  left: auto;
}
.mint-popup-bottom {
  top: auto;
  right: auto;
  max-height: 40%;
  bottom: 0;
  width: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
  transform: translate3d(-50%, 0, 0);
  padding: 0px;
  margin: 0px;
  font-family: "PingFangSC-Regular";
  font-size: 18px;
  color: #444444;
  text-align: center;
}
.pop_top {
  display: flex;
}
</style>
