Vue.prototype.getQueryString = function (data, url) {
    var regex = new RegExp("(^|&)" + data + "=([^&]*)(&|$)", "i"),
        query = url.toString().split("?")[1];
    if (query) {
        let match = query.match(regex);
        return match && decodeURIComponent(match[2]);
    }
    return null;
};
// iframe
Vue.prototype.getiframeString = function getQueryStr(sArgName) {
    var args = LocString.split("?");
    var retval = "";
    if (args[0] == LocString) {
        /**/
        return retval; /**/
    }
    var str = args[1];
    args = str.split("&");
    for (var i = 0; i < args.length; i++) {
        str = args[i];
        var arg = str.split("=");
        if (arg.length <= 1) continue;
        if (arg[0] == sArgName) retval = arg[1];
    }
    return retval;
};
