<template>
<div>
     <div class="popup animated"  :class="{slideInUp:popupVisible,slideOutDown:!popupVisible}" >
          <v-span :dataArr=data @popTop=popTop> </v-span>
     </div>
         <div :class="{model:popupVisible}"></div>
</div>
</template>
<script>
import vSpan from "./span.vue";
export default {
  data() {
    return {};
  },
  components: { vSpan },
  props: ["showpop", "data"],
  created() {
    console.log(this.showpop, this.data);
  },
  computed:{
     popupVisible: {
      get() {
      
        return this.showpop;
      },
      set(newVal) {
        return newVal;
      }
    } 
  },
  methods: {
    popTop(val) {
      // this.showpop=false;
      this.$emit("popVal", val);
    }
  }
};
</script>
<style lang="scss" scoped>
.popup {
  position: fixed;
  width: 100%;
  bottom: 0px;
  max-height: 40%;
  overflow-y: scroll;
  box-sizing: border-box;
  background: white;
  z-index: 1000;
}
.model {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: #000;
  display: block;
}
</style>