
<template>
  <div class="m-map">
    <div class="search" v-if="placeSearch">
      <input type="text" placeholder="" v-model="searchKey">
      <button type="button" @click="handleSearch"></button>
      <div id="js-result" v-show="searchKey" class="result"></div>
    </div>
    <div id="js-container" class="map"></div>
  </div>
</template>

<script>
import remoteLoad from '../remoteLoad.js'
// import { MapKey, MapCityName } from '@/config/config'
export default {
  props: ['lat', 'lng'],
  data () {
    return {
      searchKey: '',
      placeSearch: null,
      dragStatus: false,
      AMapUI: null,
      AMap: null
    }
  },
  watch: {
    searchKey () {
      if (this.searchKey === '') {
        this.placeSearch.clear()
      }
    }
  },
  methods: {
    // 
    handleSearch () {
      if (this.searchKey) {
        this.placeSearch.search(this.searchKey)
      }
    },
    // 
    initMap () {
      // PositionPickerloadUI 'ui/' 
      let AMapUI = this.AMapUI = window.AMapUI;
      let AMap = this.AMap = window.AMap;

      AMapUI.loadUI(['misc/PositionPicker'], PositionPicker => {
        let mapConfig = {
          zoom: 16,
          cityName: ""
        }
        // if (this.lat && this.lng) {
        //   mapConfig.center = [this.lng, this.lat]
        // }
        let map = new AMap.Map('js-container', mapConfig)
        // 
       AMap.service('AMap.PlaceSearch', () => {
          this.placeSearch =  new AMap.PlaceSearch({
            pageSize: 5,
            pageIndex: 1,
            citylimit: true,
            city: "",
            map: map,
            panel: 'js-result'
          })
        })
        // 
        // AMap.plugin(['AMap.ToolBar'],async function () {
        //    map.addControl(new AMap.ToolBar({
        //     position: 'RB'
        //   }))
        // })
        // 
        // let positionPicker = new PositionPicker({
        //   mode: 'dragMap', // 'dragMap''dragMarker''dragMap'
        //   map: map // 
        // })
        // //  drag 
        // positionPicker.on('success', positionResult => {
        //   // 
        //   if (!this.dragStatus) {
        //     this.dragStatus = true
        //   } else {
        //     this.$emit('drag', positionResult)
        //   }
        // })
        // // 
        // positionPicker.start()
      })
    }
  },
  async  beforeMount () {
    // API
    if (window.AMap && window.AMapUI) {
       this.initMap()
    // APIAPI
    }else{
       await remoteLoad(
        `http://webapi.amap.com/maps?v=1.3&key=91ad0aeac9e4261c2c0f1e07e942a2a4`
      );
      await remoteLoad("http://webapi.amap.com/ui/1.0/main.js");
      this.initMap();
    }
  }
}
</script>

<style lang="css" scoped>
#js-container {
  width: 100%;
  height: 500px;
}
.m-map {
  min-width: 500px;
  min-height: 300px;
  position: relative;
}
.m-map .map {
  width: 100%;
  height: 100%;
}
.m-map .search {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 285px;
  z-index: 1;
}
.m-map .search input {
  width: 180px;
  border: 1px solid #ccc;
  line-height: 20px;
  padding: 5px;
  outline: none;
}
.m-map .search button {
  line-height: 26px;
  background: #fff;
  border: 1px solid #ccc;
  width: 50px;
  text-align: center;
}
.m-map .result {
  max-height: 300px;
  overflow: auto;
  margin-top: 10px;
}
</style>
