<template>
    <div class="ldinput">
        <div v-if="!isSearch" class="ldinput_form" :class="{'underLine':line}">
            <div class="label">{{label}}</div>
            <el-input :value="valueInner" :disabled=disabledInner :placeholder="placeholder" @focus="funFoc" @blur="funBlur" @input.native="funIpt">
                <i v-show="isShowIcon&&!icon" slot="suffix" :class="closeIconInner" @click="clickIcon">

                </i>
                <i v-if="icon&&!$slots.icon" slot="suffix" :class="'el-icon-'+icon">

                </i>

                <slot name="icon" slot="suffix" v-if="$slots.icon">

                </slot>
            </el-input>

        </div>
        <div v-if="isSearch" class="ldinput_search" :class="{'underLine':line}">
            <el-input :value="valueInner" :placeholder="placeholder" @input.native="funIpt" prefix-icon="el-icon-search">
                <i v-show="isShowIcon" slot="suffix" class="el-icon-circle-close" @click="clickIcon">

                </i>
            </el-input>

        </div>

    </div>
</template>

<script>
export default {
  data() {
    return {
      line: false, //
      isShowIcon: false, //icon
      valueInner: this.value, // 
      iconFlag: 0 //0:icon 1:
    };
  },
  methods: {
    clickIcon() {
      this.isShowIcon = false;
      this.valueInner = "";
      this.$emit("clickIcon");
      this.$emit("input", "");
    },
    funFoc() {
      this.line = true;
      this.$emit("ldFocus");
    },
    funBlur() {
      this.line = false;
    },
    funIpt(event) {
      const value = event.target.value;
      this.$emit("input", value);
    }
  },
  watch: {
    value(val) {
      this.valueInner = val;
    },
    valueInner(val) {
      this.isShowIcon = val;
    }
  },
  computed: {
    closeIconInner() {
      return this.closeIcon !== undefined ? "el-icon-circle-close" : "";
    },
    disabledInner() {
      //[disabled]===true   accept true Or false
      return this.disabled !== undefined
        ? this.disabled === false ? false : true
        : false;
    },
    isSearch() {
      //show the searchInput  if that attr ===true ignore other attr but $emit clickIcon
      return this.search !== undefined ? true : false;
    }
  },
  props: {
    value: {
      required: true,
      defalut: ""
    },
    label: {
      required: false,
      defalut: ""
    },
    closeIcon: {
      required: false
    },
    icon: {
      required: false
    },
    disabled: {
      required: false,
      defalut: true
    },
    placeholder: {
      required: false,
      defalut: ""
    },
    search: {
      required: false,
      defalut: ""
    }
  },
  mounted() {
    if (this.value) {
      this.isShowIcon = true;
    }
    console.log();
  }
};
</script>

<style lang="scss">
.ldinput_form {
  width: 100%;
  line-height: 52px;
  // padding-left: 20px;
  border-bottom: 1.5px solid #f7f7f7;
  -webkit-transition: border 0.5s;
  -moz-transition: border 0.5s;
  -ms-transition: border 0.5s;
  -o-transition: border 0.5s;
  transition: border 0.5s;
  &.underLine {
    border-bottom: 1px solid #0484fa;
  }
  > .label {
    box-sizing: border-box;
    float: left;
    width: 66px;
    font-family: ".PingFangSC-Regular";
    font-size: 14px;
    color: #444444;
    letter-spacing: 0;
    // text-align: left;
    // text-align: center;
  }
  > .el-input {
    font-size: 14px;
    overflow: auto;
    width: auto;
    display: block !important;
    &.is-disabled {
      .el-input__inner {
        background-color: #fff;
      }
    }
    .el-input__inner {
      padding-left: 5px;
      border: none;
      &::-webkit-input-placeholder {
        font-weight: 200;
      }
    }
    .el-input__suffix-inner {
      cursor: pointer;
      i {
        font-size: 14px;
      }
      .el-icon-circle-close {
        &:hover {
          color: darken(#ccc, 10%);
        }
      }
    }
  }
}

.ldinput_search {
  line-height: 36px;
  border-bottom: none;
  > .el-input {
    font-size: 14px;
    width: auto;
    display: block !important;
    .el-input__inner {
      font-size: 14px;
      border: none;
    }
    i {
      font-size: 14px;
    }
    .el-input__suffix-inner {
      cursor: pointer;
      i {
        font-size: 14px;
      }
      .el-icon-circle-close {
        &:hover {
          color: darken(#ccc, 10%);
        }
      }
    }
  }
}
</style>
