<template>
  <div class="card" @click="change">
    <div :class="{bg:isActivebg}">
      <div class="content">
        <div class="big_text">
          <span>{{trade}}</span>
          <span :class="{tip_txt1:spanomit,tip_txt2:!spanomit}" v-if="tipdays">{{tipdays}}</span>
          <span class="change_txt" v-if="spanomit" @click="omitclick">...</span>
          <span class="change_txt2" v-if="approve" >{{approve}} </span>
        </div>
        <div class="icon_text">
          <div class="item">
            <img src="/static/imgs/icon_content_label.png" alt="" v-if=!grey>
            <img src="/static/imgs/icon_content_location_grey.png" alt="" v-if=grey>
            <span>{{traffichub}}</span>
            <div class="iphone" v-if="iphone">
              <img src="/static/imgs/icon_content_phone.png" alt="">
              <span>{{iphone}}</span>
            </div>
          </div>
          <div class="item">
            <img src="/static/imgs/icon_content_location.png" alt="" v-if=!grey>
            <img src="/static/imgs/icon_content_location_grey.png" alt="" v-if=grey>
            <span>{{adress}}</span>
          </div>
        </div>
      </div>
      <div class="item_big" v-if=!newadress>
        <img src="/static/imgs/icon_content_tips.png" alt="" v-if=!grey>
        <img src="/static/imgs/icon_content_tips_grey.png" alt="" v-if=grey>
        <span class="oneself" v-if="oneself"> </span>
        <span :class="{txtcolor:txtcolor}">{{privatesea}}</span>
      </div>
    </div>
  </div>
</template>

<script>
// mode 1---
// mode 2--
// mode 3--
// 3
export default {
  props: [
    "trade", //trade//
    "adress", //
    "traffichub", //
    "privatesea", //
    "mode", //mode 1   2 3
    "newadress", //
    "follow",
    "request", //
    "spanomit", //
    "tipdays", //
    "iphone", //
    "oneself", ///
    "approve",//"BM"
  ],
  data() {
    return {
      isActivebg: false,
      txtcolor: true,
      grey: false
    };
  },
  mounted() {
    this.changemode();
  },
  methods: {
    change() {
      this.$emit("change");
    },
    omitclick() {
      this.$emit("omitclick");
    },
    changemode() {
      switch (this.mode) {
        case "2":
          this.isActivebg = true;
          this.grey = true;
          this.txtcolor = false;
          break;
        case "1":
          this.grey = false;
          this.isActivebg = false;
          this.txtcolor = true;
          break;
        case "3":
          this.grey = false;
          this.isActivebg = false;
          this.txtcolor = false;
        default:
          this.grey = false;
          this.txtcolor = false;
          break;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
* {
  margin: 0px;
  padding: 0px;
  background: white;
}

.card {
  // box-sizing: content-box;
  font-family: ".PingFangSC-Regular";
  width: 100%;
  height: auto;
  border-bottom: 10px solid #f7f7f7;
  .bg {
    background: #fcfcfc;
  }
  .content {
    height: 142px;
    padding-left: 20px;
    padding-right: 19px;
  }

  .big_text {
    display: block;
    padding-top: 24px;
    padding-bottom: 20px;
    font-family: ".PingFangSC-Regular";
    font-size: 16px;
    color: #444444;
    letter-spacing: 0;
    // line-height: 16px;
    // text-align: left;
  }
  .tip_txt1 {
    display: inline-block;
    font-size: 12px;
    color: #c06880;
    letter-spacing: 0;
    line-height: 12px;
    text-align: center;
    margin-left: 7px;
    background: #ffeff1;
    line-height: 16px;
    width: 37px;
    height: 16px;
  }
  .tip_txt2 {
    display: inline-block;
    float: right;
    font-size: 12px;
    color: #c06880;
    letter-spacing: 0;
    line-height: 12px;
    text-align: center;
    background: #ffeff1;
    width: 37px;
    height: 16px;
  }
  .change_txt {
    display: inline-block;
    font-size: 26px;
    line-height: 5px;
    float: right;
    color: #999999;
  }
  .change_txt2{
     display: inline-block;
    font-size: 12px;
    float: right;
    color: #999999;
  }
  .item {
    display: block;
    box-sizing: content-box;
    padding-bottom: 12px;
    font-size: 12px;
    color: #999999;
    letter-spacing: 0;
    line-height: 17px;
    text-align: left;
    span {
      padding-left: 5px;
    }
    img {
      width: 10px;
      // height: 12px;
      vertical-align: middle;
    }
    .iphone {
      margin-left: 57px;
      // span {
      //   padding: 0px;
      // }
      display: inline-block;
    }
  }
  .item_big {
    padding-left: 20px;
    padding-right: 19px;
    border-top: 2px solid #f7f7f7;
    font-size: 14px;
    height: 52px;
    display: flex;
    color: #999999;
    align-items: center;
    span {
      padding-left: 8px;
    }
    .txtcolor {
      color: #0484fa;
    }
    img {
      width: 11px;
      height: 12px;
      vertical-align: middle;
    }
    .oneself {
      padding: 0px;
      display: inline-block;
      background: #ffeff1;
      width: 37px;
      height: 16px;
      text-align: center;
      font-size: 12px;
      color: #c06880;
      letter-spacing: 0;
    }
  }
}
</style>

