<template>
    <div class="ld-address">
        <span v-if="!$slots.icon&&!bol_spantext" class="ld_address_prefix" :class="{hide:bol_spantext}">{{prefix}}</span>
        <slot v-if="$slots.icon&&bol_spantext" class="ld_address_prefix" :class="{hide:bol_spantext}" name=prefix>{{prefix}}</slot>
        <el-input type="textarea" placeholder=""  @input.native="funIpt" resize=none :value='value'></el-input>

    </div>
</template>

<script>
export default {
  data() {
    return {
      //   hide_bol:false
    };
  },
  computed: {
    bol_spantext: {
      get() {
        if (!!this.spantext) {
          this.$slots = {};
        }
        return !!this.spantext;
      },
      set(val) {
        if (!!val) {
          this.$slots = [];
        }
        return !!val;
      }
    }
  },
  methods: {
    funIpt(event) {
      const value = event.target.value;
      this.$emit("input", value);
      //
      var fullValue = event.target.value;
      var perfixReg = new RegExp(this.prefix);
      if (!perfixReg.test(fullValue)) {
        fullValue = this.prefix + fullValue;
      }
      this.$emit("fullAddress", fullValue);
    }
  },
  props: {
    value: {
      required: true,
      defalut: ""
    },
    prefix: {},
    spantext: {}
  },
  mounted() {
    var temIndent =
      this.$el.querySelector(".ld_address_prefix").clientWidth + "px";
    this.$el.querySelectorAll("textarea").forEach(item => {
      item.style.textIndent = temIndent;
    });
  }
};
</script>

<style lang="scss">
.hide {
  display: none;
}
.ld-address {
  position: relative;
  height: 80px;
  padding: 17px 0;
  padding-bottom: 5px;
  .ld_address_prefix {
    position: absolute;
    top: 17px;
    left: 20px;
    font-size: 14px;
    line-height: 18px;
    color: #999;
  }
  .el-textarea {
    height: 100%;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    .el-textarea__inner {
      font-size: 14px;
      line-height: 18px;
      height: 100%;
      padding: 0;
    }
  }
}
</style>
