import Vue from 'vue'
import http from './http'

Vue.mixin({
    beforeCreate() {
        const options = this.$options;
        if (options.lib)
            this.lib = options.lib;
        else if (options.parent && options.parent.lib)
            this.lib = options.parent.lib;
    }
})
//cookie
Vue.prototype.lcookie = {
    setCookies: function(name, value, days) {
        //
        function getCookieDomain() {
            var host = location.hostname;
            var ip = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
            if (ip.test(host) === true || host === "localhost") return host;
            var regex = /([^]*).*/;
            var match = host.match(regex);
            if (typeof match !== "undefined" && null !== match) host = match[1];
            if (typeof host !== "undefined" && null !== host) {
                var strAry = host.split(".");
                if (strAry.length > 1) {
                    host = strAry[strAry.length - 2] + "." + strAry[strAry.length - 1];
                }
            }
            return "." + host;
        }
        var d = new Date();
        d.setTime(d.getTime() + 24 * 60 * 60 * 1000 * days);
        window.document.cookie =
            name +
            "=" +
            value +
            ";expires=" +
            d.toGMTString() +
            ";domain=" +
            getCookieDomain() +
            ";path=/";
    },
    getCookies: function(name) {
        var arr,
            reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        if ((arr = document.cookie.match(reg))) return unescape(arr[2]);
        else return null;
    },
    removeCookies: function(name) {
        this.setCookies(name, "", -1);
    }
};
//
Vue.prototype.accMul = function(arg1, arg2) {
    var m = 0,
        s1 = arg1.toString(),
        s2 = arg2.toString();
    try {
        m += s1.split(".")[1].length;
    } catch (e) {}
    try {
        m += s2.split(".")[1].length;
    } catch (e) {}
    return (
        Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)
    );
};
// 
Vue.prototype.returnToSuperior = function() {
    window.history.back();
};
// 
Vue.prototype.isEmptyObject = function(e) {
    for (let i in e) return false;
    return true;
};
//  PC
Vue.prototype.isPc = function() {
    let userAgentInfo = window.navigator.userAgent;
    const Agents = [
        "Android",
        "iPhone",
        "SymbianOS",
        "Windows Phone",
        "iPad",
        "iPod"
    ];
    let flag = true;
    for (let v = 0, len = Agents.length; v < len; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
            flag = false;
            break;
        }
    }
    return flag;
};
//
Date.prototype.formatDate = function(fmt) {
    if (isNaN(+this)) return '';
    let o = {
        "M+": this.getMonth() + 1, //
        "d+": this.getDate(), //
        "h+": this.getHours(), //
        "m+": this.getMinutes(), //
        "s+": this.getSeconds(), //
        "q+": Math.floor((this.getMonth() + 3) / 3), //
        S: this.getMilliseconds() //
    };
    if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(
            RegExp.$1,
            (this.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
    }
    for (let k in o) {
        if (new RegExp("(" + k + ")").test(fmt)) {
            fmt = fmt.replace(
                RegExp.$1,
                RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
            );
        }
    }
    return fmt;
};

export default {
    http: http
}
