<template>
    <div class="dtcard">
        <ul class="navbar line">
            <li :class="{activecolor: req}" @click="change('req')">
                <span></span>
            </li>
            <li :class="{activecolor: his}" @click="change('his')">
                <span></span>
            </li>
        </ul>

    </div>
</template>
<script>
export default {
  data() {
    return {
      item1: "hhel",
      isActive: false,
      req: false,
      his: true
    };
  },
  props: ["navitem", "objitem"],
  methods: {
    change(val) {
      if (val == "req") {
        this.req = true;
        this.his = false;
        this.$emit("change","follw")
      } else {
        this.req = false;
        this.his = true;
        this.$emit("change","req")
      }
    }
  }
};
</script>
