<template>
    <div class="navi" :class="{ expanded: expanded }">
        <div class="menu-icon" @click="toggleSlide">
            <img src="static/imgs/networkmanagement_open.png">
        </div>
        <div class="breadcrumb">
            <el-breadcrumb separator=">" separatorClass="el-icon-arrow-right">
                <el-breadcrumb-item
                        :class='{currentBread:index==breads.length-1}'
                        v-for="(item, index) in breads"
                        :key="index"
                        :to="item">
                    {{ item.label }}
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="slide">
            <div class="banner">
                <div class="menu-icon" @click="toggleSlide">
                    <img src="static/imgs/networkmanagement_open_gray.png">
                </div>
                <div class="caption">{{systemName}}</div>
                <div class="account">
                    <span>{{userName}}</span>
                    <img src="static/imgs/icon_title_back.png">
                </div>
            </div>
            <div class="main">
                <div class="menu">
                    <div class="item"
                         v-for="(menu,i) in menus.sub" :key="i"
                         v-if="menu.renderTree"
                         @click="switchMenu(menu.path)"
                         :class="{ checked: ~currentExpandedPath.indexOf(menu.path) }">
                        <span>{{menu.meta.level}}</span>
                    </div>
                </div>
                <div class="sub">
                    <div class="item"
                         :class="{ checked:~currentExpandedPath.indexOf( sub.path) }"  
                         @click="jumpTo(sub.path)"
                         v-for="sub in subMenus" >
                        <span>{{ sub.meta.level.split('/').pop() }}</span>
                    </div>
                </div>
            </div>
            <div class="switch">
                <span></span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                breads: [], // 
                menus: [], // ()
                currentPath: '', // 
                currentExpandedPath: '', // 
                subMenus: null,
                expanded: false,
                userName:this.$store.state.ui.user.name,
                systemName:this.$store.state.ui.system.name
            }
        },
        methods: {
            toggleSlide() {
                this.expanded = !this.expanded;
                // console.log(this.breads);
            },
            switchMenu(path) {
                this.currentExpandedPath = path;
                this.menus.sub.forEach(item => {
                    if (item.path === path) {
                       if( item.meta.singleRouter){
                           this.subMenus=[];
                           this.$router.push(path);
                       }else{
                           this.subMenus = item.sub;

                       }
                    }
                });
            },
            jumpTo(path) {
                this.$router.push(path);
            }
        },
        beforeMount() {
            // 
            var route = this.$router.currentRoute,
                breads = [];
            console.log(route);
            var paths = route.path.split('/').slice(1),
                labels = route.meta.level.split('/');
            paths.forEach((bread, index) => {
                    breads.push({
                        label: labels[index],
                        path: '/' + paths.slice(0, index + 1).join('/')
                    })

            });
            if(!route.meta.singleRouter){
                breads.shift();
            }
            
            this.breads = breads;
             console.log(breads);
            // 
            this.menus = this.$store.state.routeTree[0];
            this.currentPath = this.$router.currentRoute.path;
            this.currentExpandedPath = this.currentPath;
           if(!route.meta.singleRouter){
               this.menus.sub.forEach(item => {
                   if (~this.currentPath.indexOf(item.path)) this.subMenus = item.sub;
               });
           }
        }
    }
</script>

<style lang="scss">
    .navi {
        height: 60px;
        line-height: 69px;
        padding: 0 20px;
        background: rgba(250, 250, 250, .9);
        color: #444444;
        font-size: 14px;
        &.expanded .slide {
            left: 0;
            right: 0;
        }

        .menu-icon {
            display: inline-block;
            img {
                width: 19px;
                height: 17px;
                display: inline-block;
                margin-right: 8px;
            }
        }
        .breadcrumb {
            display: inline-block;
            .el-breadcrumb__item {
                .el-breadcrumb__inner.is-link {
                    color: #999;
                    font-size: 16px;
                    font-weight: normal;
                    &:hover{
                        color:#0484FA ;
                    }

                }

                &.currentBread {
                    .el-breadcrumb__inner.is-link {

                        font-size: 18px;
                        font-weight: bold;
                        color: #444;
                    }

                }

            }
            .el-icon-arrow-right{
                font-size: 16px;
                font-weight: 800;
                vertical-align: baseline;
            }
        }
        .slide {
            position: absolute;
            left: -100%;
            right: 100%;
            top: 0;
            bottom: 0;
            background: #FFFFFF;
            transition: all .6s;
            overflow: hidden;
            z-index: 9999;
            .banner {
                height: 60px;
                line-height: 60px;
                padding: 0 20px;
                background: #FAFAFA;
                border-bottom: 1px solid #EAEAEA;

                .menu-icon, .caption, .account {
                    display: inline-block;
                }
                .menu-icon {
                    img {
                        transform: rotate(90deg);
                    }
                }
                .caption {
                    font-size: 20px;
                    color: #444444;
                }
                .account {
                    font-size: 14px;
                    color: #999999;
                    float: right;

                    img {
                        width: 8px;
                        height: 13.33px;
                        transform: rotate(180deg);
                        vertical-align: -2%;
                        margin-left: 5px;
                        display: inline-block;
                    }
                }
            }
            .main {
                position: absolute;
                top: 61px;
                left: 0;
                right: 0;
                bottom: 50px;
                background: #FFFFFF;

                .menu {
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    width: 130px;
                    background: #F7F7F7;

                    .item {
                        cursor: pointer;
                        height: 60px;
                        line-height: 60px;
                        padding-left: 24px;
                        &.checked {
                            position: relative;
                            background: #FFFFFF;
                            &::before {
                                position: absolute;
                                content: '';
                                left: 0;
                                top: 0;
                                bottom: 0;
                                width: 3px;
                                background: #0484FA;
                            }
                        }
                    }
                }
                .sub {
                    position: absolute;
                    top: 0;
                    left: 130px;
                    right: 0;
                    bottom: 0;

                    .item {
                        cursor: pointer;
                        height: 60px;
                        line-height: 60px;
                        padding-left: 32px;
                        &.checked {
                            color: #2F7DE1;;
                        }
                    }
                }
            }
            .switch {
                position: absolute;
                left: 0;
                bottom: 0;
                right: 0;
                height: 50px;
                line-height: 50px;
                border-top: 1px solid #EAEAEA;
                text-align: center;
            }
        }
    }
</style>
